<template>
  <div class="input input-field">
    <input
      :id="id"
      type="text"
      class="validate"
      :value="value"
			:required="required"
      v-on:input="updateValue($event.target.value)"
    /> 
    <label :for="id">{{ placeholder }}</label>
    <span class="helper-text" data-error="Заполните это поле"></span>
  </div>
</template>

<script>
export default {
  props: ["id", "value", "placeholder", 'required'],
  methods: {
    updateValue(value) {
      this.$emit("input", value);
    }
  }
};
</script>

<style lang='scss' scoped>
.input {
  margin-top: 4rem;
}
</style>